import React, { Component } from 'react';
import './css/doll.css';
import DollBody from './img/doll.png';
import BlueSuit from './img/blueSuit.png';
import FullBody from './img/fullBody.png';
import Hat from './img/hat.png';
import GreenDress from './img/greenDress.png';
import LightGreenDress from './img/lightGreenDress.png';
import OrangeDress from './img/orangeDress.png';
import BrownDress from './img/brownDress.png';

export default class Doll extends Component {
	constructor(props) {
		super(props);
		this.state = {
			id: "",
			item: false
		}
	}



	move = (e) => {
		console.log(e.target.id);
		let img = document.getElementById(e.target.id);
		document.onmousedown = () => { return false };
		img.style.cursor = 'move';
		document.onmousemove = (e) => {
			let x = e.pageX;
			let y = e.pageY;
			// let x = e.clientX;
			// let y = e.clientY;
			let left = img.offsetLeft;
			let top = img.offsetTop;
			left = x - left;
			top = y - top;
			document.onmousemove = (e) => {
				x = e.pageX;
				y = e.pageY;
				// x = e.clientX;
				// y = e.clientY;
				let dBody = document.getElementById('dollBody').getBoundingClientRect();
				// console.log(dBody.left, dBody.top, dBody.right, dBody.bottom);
				console.log("main: ", dBody.left.toFixed(0), dBody.top.toFixed(0));
				console.log("mouse:", e.pageX, e.pageY);
				console.log(dBody.top + window.pageYOffset);
				// console.log("mouse:", img.getBoundingClientRect().left.toFixed(0), img.getBoundingClientRect().top.toFixed(0));
				if (x > (dBody.left) && y > (dBody.top + window.pageYOffset) - 50 && x < (dBody.left + 150)) {

					if (!this.state.item) {
						console.log('empty');
					} else if (this.state.item && this.state.id && this.state.id !== e.target.id && e.target.id !== "hat") {
						let oldItem = document.getElementById(this.state.id);
						oldItem.style.transform = 'rotate(0deg)';
						this.setState({ id: null, item: false })
						// oldItem.onmouseup = () => {


						switch (oldItem.id) {
							// case 'hat':
							// 	break;
							case 'blueSuit':
								oldItem.style.top = 40 + '%';
								oldItem.style.left = 55 + '%';
								// this.setState({ id: null, item: false })
								break;
							case 'fullBody':
								oldItem.style.left = 15 + '%';
								oldItem.style.top = 0 + 'px';
								// this.setState({ id: null, item: false })
								break;
							case 'greenDress':
								oldItem.style.top = 65 + '%';
								oldItem.style.left = 200 + 'px';
								// this.setState({ id: null, item: false })
								break;
							case 'lightGreenDress':
								oldItem.style.left = 35 + '%';
								oldItem.style.top = 45 + '%';
								// this.setState({ id: null, item: false })
								break;
							case 'orangeDress':
								oldItem.style.left = 0 + 'px';
								oldItem.style.top = 50 + '%';
								// this.setState({ id: null, item: false })
								break;
							case 'brownDress':
								oldItem.style.top = 50 + 'px';
								oldItem.style.left = 60 + '%';
								// this.setState({ id: null, item: false })
								break;
							default:
								break;
						}
						// }
						// 	console.log('busy', this.state.id);
					}
				}
				if (x > (dBody.left + 50) && y > (dBody.top + window.pageYOffset) && x < (dBody.left + 200)) {
					this.setState({ id: e.target.id, item: true })
					img.style.transition = '.3s';
					switch (e.target.id) {
						case 'blueSuit':
							img.style.top = 339 + 'px';
							img.style.left = 693 + 'px';
							img.style.zIndex = 3;
							img.style.transform = 'rotate(9deg)';
							break;
						case 'fullBody':
							img.style.top = 330 + 'px';
							img.style.left = 711 + 'px';
							img.style.zIndex = 3;
							img.style.transform = 'rotate(-17deg)';
							break;
						case 'hat':
							img.style.top = 225 + 'px';
							img.style.left = 690 + 'px';
							img.style.zIndex = 3;
							break;
						case 'greenDress':
							img.style.top = 339 + 'px';
							img.style.left = 715 + 'px';
							img.style.zIndex = 3;
							img.style.transform = 'rotate(20deg)';
							break;
						case 'lightGreenDress':
							img.style.top = 344 + 'px';
							img.style.left = 700 + 'px';
							img.style.zIndex = 3;
							img.style.transform = 'rotate(-21deg)';
							break;
						case 'orangeDress':
							img.style.top = 339 + 'px';
							img.style.left = 709 + 'px';
							img.style.zIndex = 3;
							img.style.transform = 'rotate(25deg)';
							break;
						case 'brownDress':
							img.style.top = 339 + 'px';
							img.style.left = 693 + 'px';
							img.style.zIndex = 3;
							img.style.transform = 'rotate(-7deg)';
							break;
						default:
							break;
					}


				} else {
					img.style.zIndex = 1;
					img.style.top = y - top + 'px';
					img.style.left = x - left + 'px';
					img.style.transform = 'rotate(0deg)';
					img.style.transition = 'none';
				}

			}

		}
		document.onmouseup = () => {
			img.style.zIndex = 1;
			img.style.cursor = 'auto';
			document.onmousedown = () => { };
			document.onmousemove = () => { };
		}
	}

	render() {
		return (
			<div id='stand' >
				<img id='dollBody' src={DollBody} alt="doll body" />
				<img id='blueSuit' src={BlueSuit} alt="doll body" onMouseDown={(e) => this.move(e)} />
				<img id='fullBody' src={FullBody} alt="doll body" onMouseDown={(e) => this.move(e)} />
				<img id='hat' src={Hat} alt="doll body" onMouseDown={(e) => this.move(e)} />
				<img id='greenDress' src={GreenDress} alt="doll body" onMouseDown={(e) => this.move(e)} />
				<img id='lightGreenDress' src={LightGreenDress} alt="doll body" onMouseDown={(e) => this.move(e)} />
				<img id='orangeDress' src={OrangeDress} alt="doll body" onMouseDown={(e) => this.move(e)} />
				<img id='brownDress' src={BrownDress} alt="doll body" onMouseDown={(e) => this.move(e)} />
			</div>
		)
	}
}
